.photo-item-wrapper {
  display: grid;
  grid-template-columns: 80px auto;
  width: 100%;
  height: 80px;
  border: solid 0.1px #ddd;
  background-color: #fff;

  & .photo-image {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1.8em;
    background-color: #eee;
    border-radius: 50%;
    width: 80%;
    height: 80%;
    margin: auto;
  }

  & .photo-data {
    display: grid;
    grid-template-columns: 80% 20%;
    text-align: left;
    padding-left: 0.5em;
    & h2,
    h3 {
      font-weight: 400;
      margin: 0;
    }
    & .photo-data-meta {
      width: 100%;
      margin: auto 0;
      overflow: hidden;
      & .photo-name,
      & .photo-type {
        margin: 0;
        max-width: 150px;
        display: inline-block;
        font-size: 1.3em;
      }
      & .photo-name {
        font-weight: 700;

        &.small-text {
          font-size: 1em;
        }

        &.extra-small-text {
          font-size: 0.7em;
        }
      }

      & .photo-type {
        margin-left: 15px;
        line-height: 1.3em;
        background-color: #eee;
        font-size: 1.1em;
        padding: 0 10px;
      }

      & .photo-costume {
        font-size: 1em;
      }
    }

    & .photo-data-type,
    & .photo-data-number {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    & .photo-data-number {
      & h3 {
        font-size: 1.3em;
        margin-left: auto;
        margin-right: 20px;
      }
    }
  }
}

.SwipeableListItem {
}
.photo-item-swipe-del-div {
  display: grid;
  grid-template-columns: 80% 20%;
  font-weight: 700;
  height: 100%;
  width: 100%;
  background-color: #ff8888;

  & h3 {
    margin: auto;
    line-height: 100%;
  }
}
