footer {
  display: flex;
  justify-content: center;
  position: fixed;
  height: 50px;
  width: 100%;
  bottom: 0;
  z-index: 1;
  border-top: 1px solid #ddd;
  background-color: #fff;

  & .button-wrapper {
    display: inline-block;
    width: calc((100vw - 20px) / 3);

    & button {
      color: #888;
      width: 100%;
      height: 100%;
      background-color: #fff;
      border: none;

      &:focus {
        outline: none;
      }

      &:active {
        background-color: #aaa;
      }

      & svg {
        display: block;
        margin: 5px auto 0 auto;
      }
      &.selected {
        color: #000;
      }
    }

    & .button-text-container {
      display: flex;
      justify-content: center;
      & svg {
        margin: auto 0;
        display: inline-block;
        font-size: 1em;
      }
    }
  }
}
